.lender-display {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 10px;
}

h1,
h3 {
  margin-top: 10px;
  margin-bottom: 10px;
}

@media (max-width: 700px) {
  h1 {
    font-size: 20px;
  }

  h3 {
    font-size: 12px;
  }

  h1,
  h3 {
    margin-top: 5px;
    margin-bottom: 5px;
  }
}
